.app{
    padding-top:5rem ;
    display:flex;
    justify-content: center;
}
.wrap{
    width: 500px;
}
$height:46px;
.select {
    width: 100%;
    position: relative;

    &.open{
        .select__dropdown{
            display: block;
        }
    }
    .select__input{
        border-bottom: none;
    }
    &__input{
        border:1px solid #ccc;
        border-radius: 5px;
        height: $height;
        display: flex;
        align-items:  center;
        padding: 0 1rem;
    }
    &__dropdown{
        display: none;
        position: absolute;
        border:1px solid #ccc;
        border-radius: 5px;
        top:$height;
        left: 0;
        right: 0;
        max-height: 200px;
        overflow-y: auto;
        box-shadow: 2px 3px 5 px rgba(0,0,0,.3);
    }
    &__list{
        padding: 0;
        margin: 0;
        list-style: none;
    }
    &__item{
        padding: 1rem;
        border-bottom: 1px solid rgb(0, 0, 0);
    }
    }